<template>
  <div class="">
    <div>video</div>
    <div>图片懒加载测试</div>
    <input type="file" ref="fileRef" name="file77">
    <h1 @click="submit">FormData格式发送图片给后端</h1>
    <img v-for="item in imgList" v-lazy='item' :key="item" alt="" />
  </div>
</template>

<script>
import { formUpload } from "../request/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      imgList: [
        require("@/assets/img/1.png"),
        require("@/assets/img/2.png"),
        require("@/assets/img/3.png"),
        require("@/assets/img/4.png"),
        require("@/assets/img/5.png"),
        require("@/assets/img/6.png"),
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  methods: {
    submit() {
      let file = this.$refs.fileRef.files[0]; // 获取用户提交的表单中的文件
      let formData = new window.FormData(); // vue中要加上window
      formData.append("file", file); // 创建一个FormData对象，然后通过append()方法向对象中添加文件，第一个参数对应表单中input中的name
      // 例如: 当前的name为file，表示发送的就是: <input name="file">
      formUpload(formData); // 将这个formData发送
    },
  },
};
</script>
<style lang='less' scoped>
img {
  display: block;
  margin-top: 200px;
}
</style>