import request, { formApi } from "./request.js";

// -------------------用户相关接口-------------------------------------------------------------
// let token = store.state.token;
// 如果写在这里，然后又在下面的export函数中使用，这个表达式只会在这个JS第一次被import导入时执行一次，然后这个值就固定不会变化了，除非页面刷新
// 即写在export外的数据是不会自动更新变化的，因为被导入会更新执行的只有export里的函数
// 所以解决方法是将需要更新变化的值写在export函数中，export default也一样，要把更新的数据写在export default函数里面

export const login = (user) => {
  // 登录接口
  return request({
    method: "POST",
    url: `/user/login`,
    data: user,
  });
};

export const getUser = () => {
  // let token = store.state.token;  // 应该写在这里面，这样每次调用这个函数，都会更新获取token
  return request({
    method: "GET",
    url: "/userAdmin/findUserById",
  });
};
// ---------------------频道api------------------------------------------
// -------------------获取所有频道名称----------------------------------
export const getChannelName = () => {
  return request({
    method: "GET",
    url: "/channel/channelName",
  });
};
// -------------------根据频道名称返回文章----------------------------------------
export const getChannelArticleByName = (params) => {
  return request({
    method: "GET",
    url: `/channel/findByName`,
    params,
  });
};
// -------------------点击搜索内容返回搜索结果---------------------------------------
export const getChannelArticleByContent = (content) => {
  return request({
    method: "GET",
    url: `/channel/findByContent`,
    params: {
      content,
    },
  });
};

// ------------------返回搜索建议接口-------------------------------
export const getSearchSuggest = (inputContent) => {
  return request({
    method: "GET",
    url: `/channel/search`,
    params: {
      inputContent,
    },
  });
};

export const formUpload = (data) => {
  return formApi({
    method: "POST",
    url: `upload`,
    data,
  });
};
