import axios from "axios";
import store from "@/store";
import router from "@/router";

const request = axios.create({
  baseURL: process.env.NODE_ENV === "production" ? "http://localhost:3000" : "api", // 请求的基础路径
  timeout: 8000, // 请求超时时间
});
// ------------------------------------------------------
const formApi = axios.create({
  // form表单测试请求
  baseURL: process.env.NODE_ENV === "production" ? "http://localhost:8080" : "cs",
  timeout: 8000, // 请求超时时间
});
formApi.defaults.headers.post["Content-Type"] = "application/x-www-form-urlencoded"; // 当使用formData格式发送数据给后端时，需要设置请求头的Content-Type为application/x-www-form-urlencoded
// 表示使用form表单默认的提交数据的格式，正常情况下，等价于multipart/form-data，因为表单默认的提交数据格式就是multipart/form-data
// -------------------------------------------------

request.interceptors.request.use((req) => {
  const token = store.state.token; // 因为这个js不是xx.vue的vue组件文件，所以无法使用this.$store和this.router,需要手动import导入store和router
  if (token) {
    req.headers.Authorization = `Bearer ${token}`; // 给请求都带上token
  }

  return req;
});

request.interceptors.response.use(
  (res) => {
    const { data } = res;
    if (data.code !== 1) {
      console.warn(data.msg);
    }
    return res;
  },
  (err) => {
    // 防止随便伪造一个token进入非登录页面，虽然无法发起请求，但是可以进入页面，这不够安全,---以及当token过期无效/被手动修改等以外情况时
    // 如果响应返回401，则表示token无效，清除本地token，并返回登录页
    if (err.response.status && err.response.status == 401) {
      store.commit("clearToken");
      router.push({ name: "Login" });
    }
  }
);

export default request;
export { formApi };
